import {Flex,Spinner} from '@chakra-ui/react';

const Loader =() => {
    return (
        <Flex alignItems='center' justfyContent ='center'>
            <Spinner
               thickness='4px'
               speed='0.65s'
               emptyColor='gray.200'
               color='blue.500'
               size ='xl'
               label='Loading...'
            />   
        </Flex>
    );
};

export default Loader;